@import url('https://fonts.googleapis.com/css2?family=Saira:wght@400;500;700&display=swap');

html {
  font-family: 'Saira', sans-serif;
  //! font-size: 62.5%;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}

/* Helpers */

.highlight--bold {
  font-weight: 500;
}

.highlight--positive {
  color: #43a047;
}

.highlight--negative {
  color: #ff5722;
}

/* Prerequesits */

/** lottie */
.lottie-animation {
  width: 70%;
  transition: opacity 500ms ease-in, visibility 500ms linear;

  svg {
    width: 100% !important;
  }

  &--hidden {
    opacity: 0;
    visibility: hidden;
  }
}

/* App => Container structure */

.app {
  // height: 100vh;
  height: auto;

  &__content {
    padding-top: 76px;
    padding-bottom: 90px;
  }
}

.bottom-nav {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  padding-bottom: 1rem;
  box-shadow: 0px 2px 13px 1px rgba(0, 0, 0, 0.217) !important;

  &__link {
    flex: 1;
    display: flex;
    justify-content: center;
    padding: 0 !important;
  }
}

/*! My Profile */

.my-profile {
  padding-bottom: 3rem;

  &__head {
    display: flex;
    align-items: center;
  }

  &__username {
    margin-left: 1rem;
    font-size: 1.6rem;
    font-weight: 500;
  }

  &__buttons {
    display: flex;
  }

  &__logout {
    margin-left: 2rem !important;
  }
}

.table {
  margin: 2rem 0;

  &__head {
    background-color: #eb5669;
  }

  &__head th {
    width: 100%;
    color: white;
    font-weight: 500;
    text-transform: uppercase;
  }

  &__body tr:nth-of-type(odd) {
    background-color: rgba(0, 0, 0, 0.04);
  }
}

/*! Create Recipe */

.create-recipe {
  position: relative;
  left: 0;
  padding: 0 !important;

  &__row {
    margin-top: 5px;
    margin-bottom: 15px;
  }

  &__title {
    max-width: 80%;
    padding-left: 30px;
    font-size: 1.7rem;
    font-weight: 500 !important;
  }

  &__row:last-child {
    margin-bottom: 76px;
  }

  &__slider {
    display: flex;
    overflow-x: scroll;
  }

  &__button {
    position: fixed !important;
    bottom: 90px;
    right: 30px;
  }

  &__header {
    position: relative;
    height: 76px;
  }
}

/*! Food Page => CR */

.food-page {
  &__header {
    display: flex;
    justify-content: center;
    margin-bottom: 3rem;
  }
}

.food-card {
  width: 200px;
  max-width: 200px;
  min-width: 200px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__mark {
    width: 30px;
    height: 30px;
    background-color: blue;
  }

  &__image {
    object-fit: contain;
  }

  &__content {
    display: flex;
    align-items: center;
  }

  & .MuiCardContent-root {
    padding: 4px 16px;
  }
}

.MuiBox-root {
  padding: 0 !important;
}

/*! Menu => CR */

.recipe {
  &__header {
    margin-bottom: 1.8rem;
  }

  &__title {
    color: rgba(0, 0, 0, 0.406);
  }

  &__default {
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  &__default h3 {
    font-size: 3rem;
    color: #ffd6d9;
    // color: #ffd9bc;
    font-weight: 500;
  }
}

.items-page {
  h3 {
    max-width: 70%;
    padding-left: 30px;
    font-size: 1.7rem;
    margin-bottom: 1rem;
    font-weight: 500;
  }
}

.menu-card {
  margin-bottom: 1.5rem;

  &__nutrients {
    display: flex;
    padding: 16px;
  }

  &__nutrient {
    flex: 1;

    padding-top: 0;

    p {
      font-size: 0.8rem;
      color: #858585;
    }
  }

  &__nutrient:first-child {
    flex: 2;
  }
}

.recipe-card {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}

.recipe-card:last-child {
  margin-bottom: 4rem;
}

/* Grammage Modal*/

.grammage-text {
  font-size: 2rem;
  color: #000000;
}

.recipe-nutrition {
  &__data {
    margin-top: 2.5rem;
  }

  &__vitamins {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__vitamins .data-section {
    flex-basis: 45.25%;
  }
}

/* Section Nutrients */

.data-section {
  margin: 1.5rem 0;

  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__title {
    font-size: 1.7rem;
    font-weight: 500;
  }

  p {
  }
}

.data-value {
  display: flex;
  flex: 1;
  flex-direction: column;
  flex-basis: 40.25%;
  justify-content: space-between;
  color: rgba(0, 0, 0, 0.7);
  background: #f7f6f6;
  margin: 0.5rem;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%),
    0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);

  &__value {
    font-size: 1.7rem;
    color: black;
    display: flex;
    align-items: center;

    svg {
      display: none;
      margin-left: 1rem;
    }
  }

  &--success {
    background: #efffea;
  }
  &--error {
    background: #ffeeea;
  }

  &__title {
    margin-bottom: 0;
    width: 100%;
  }

  &__spread {
    margin: 0;
  }

  &__value {
    margin: 0;
  }

  &__value--error {
    color: rgb(255, 82, 82);
  }

  &__value--warning {
    color: rgb(247, 185, 27);
  }

  &__value--success {
    color: #18be21;
  }

  &__value--deficite {
    .data-value__up {
      display: block;
    }
  }

  &__value--extensive {
    .data-value__down {
      display: block;
    }
  }
}

.auth {
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100vh;
  z-index: 9999;
  display: flex !important;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 3rem;

  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;

    .lottie-animation {
      height: 240px;
    }

    // svg {
    //   width: 240px !important;
    // }
  }

  &__logo {
    width: 170px;
  }

  &__title {
    font-weight: 500;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: 2rem;
    // text-transform: uppercase;
  }

  &__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 2rem;
  }

  &__login {
    width: 100%;
    margin-bottom: 1.5rem !important;
  }

  &__footer {
    display: flex;
    flex-direction: column;
    padding: 0 2rem;

    button {
      margin-bottom: 0.5rem;
    }
  }
}

.intro-page {
  width: 100%;
  text-align: left;
  overflow: hidden;
  // justify-content: space-around;
  // background: white;
  //   width: 100%;
  //   position: absolute;
  //   height: 100vh;
  //   left: 0;
  //   top: 0;
  //   z-index: 99999999999999999;

  &__head {
    display: flex;

    legend {
      width: 70%;
    }
  }

  &__title {
    font-size: 2.5rem;
    margin: 0.5rem 0 2rem 0;
    font-weight: 500;
  }

  .intro-page__steps {
    transition: opacity 500ms ease-in, visibility 500ms linear;
    opacity: 1;
    visibility: visible;
  }

  &--complete .intro-page__steps {
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }

  .intro-page__welcome {
    transition: opacity 500ms ease-in 1500ms, visibility 500ms linear 1500ms;
    opacity: 0;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 100%;
    top: 0;
  }

  &--complete .intro-page__welcome {
    opacity: 1;
    visibility: visible;
  }

  &__input {
    margin-bottom: 1.5rem !important;
  }

  &__finish {
    margin-bottom: 90px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: start;
  }
}

.header {
  background-color: transparent !important;
  box-shadow: none !important;
  padding: 10px 5px;

  &__toolbar {
    display: flex;
    justify-content: space-between;
  }

  &__logo {
    width: 135px;
  }
}

.modal {
  &__footer {
    display: flex;
    justify-content: flex-end;
  }
}

.menu-list {
  &__head {
    padding-bottom: 20px;
  }

  &__body {
    height: 340px;
    overflow: scroll;
  }
}

.alert-banner {
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;
  border-radius: 0 0 0px 0px;
  color: white;
  display: flex;
  align-items: center;
  padding: 1rem 2rem;
  transition: transform 300ms ease-in;
  transform: translateY(-100%);

  &--success {
    background-color: #44d54b;
  }

  &--error {
    background-color: #ff3737;
  }

  &--show {
    transform: translateY(0);
  }
}

.donut {
  &__head {
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
  }

  &__chip {
    margin: 0 0.5rem !important;
  }
}
.MuiButtonBase-root.MuiIconButton-root.MuiAutocomplete-clearIndicator.MuiAutocomplete-clearIndicatorDirty {
  display: none;
}

.MuiAppBar-colorDefault.MuiPaper-elevation4 {
  box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%),
    0px 3px 3px 0px rgb(0 0 0 / 14%), 0px 0px 0px 0px rgb(0 0 0 / 12%);
  background-color: #fff;
}

.fab__icon {
  width: 30px;
  height: 30px;
}

//! seasoning

.seasoning-modal__section {
  margin: 30px 0;
}

.seasoning-modal__chips {
  margin-top: 20px;
}

//!write a universal css for modals
.seasoning-modal__body {
  overflow: scroll;
  height: 350px;
}

