}

.MuiAvatar-colorDefault {
  background-color: #eb5669 !important;
}

.intro-page .MuiInputLabel-shrink {
  transform: translate(0, -3.5px) !important;
  transform-origin: top left;
}

.activity-info {
  &__section {
    margin: 1rem 0;

    p {
      color: rgba(0, 0, 0, 0.54);
    }
  }
}

.info-modal {
  display: inline-flex;
  margin-left: 1.5rem;
  justify-content: center;
  align-items: center;
}

